body {
  margin: 0;padding: 0;color: #666;
  font-family: Helvetica Neue,Tahoma,Arial,PingFangSC-Regular,Hiragino Sans GB,Microsoft Yahei,sans-serif;
}
a{text-decoration: none;color: #666;}
input{outline: none;}
ul, li{margin: 0;padding: 0;list-style: none;}

#nprogress .bar {background: #88c322!important;height: 4px!important;}
#nprogress .spinner-icon {border-top-color: #88c322!important;border-left-color: #88c322!important;}

.lodding{text-align: center;margin: 0.5rem 0;}
.lodding span{font-size: 0.35rem;line-height: 0.4rem;}
.lodding i{-webkit-animation: preloader-spin 1s steps(12, end) infinite;animation: preloader-spin 1s steps(12, end) infinite;color: #88c322;display: inline-block;width: 0.4rem;height: 0.4rem;font-size: 0.4rem;margin-right: 0.2rem;}
@-webkit-keyframes preloader-spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes preloader-spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}




